  const edcPath = window.configBridge.getConfig()?.edc?.path
  const printerPath = window.configBridge.getConfig()?.printer?.path

  const [receiptMode, setReceiptMode] = useState(window.localStorage.getItem(`receiptMode/${user.salePointId}`) || '')
  console.log(receiptMode)
  const [edcMode, setEdcMode] = useState((edcPath && defaultComs.includes(edcPath))
    ? edcPath
    : (edcPath === '')
      ? 'other'
      : 'COM4'
  )
  const [printerMode, setPrinterMode] = useState((printerPath && defaultComs.includes(printerPath))
    ? printerPath
    : (printerPath === '')
      ? 'other'
      : 'COM3'
  )
  const [edcValue, setEdcValue] = useState((edcPath && defaultComs.includes(edcPath))
    ? ''
    : edcPath
  )
  const [printerValue, setPrinterValue] = useState((printerPath && defaultComs.includes(printerPath))
    ? ''
    : printerPath
  )

  useEffect(() => {
    if (!edcPath && edcPath !== '') {
      window.configBridge.setConfig('edc', { path: 'COM4' })
    }
    if (!printerPath && printerPath !== '') {
      window.configBridge.setConfig('printer', { path: 'COM3' })
    }
  }, [edcPath, printerPath])

  const handleReceiptModeChange = e => {
    setReceiptMode(e.target.value)
    window.localStorage.setItem(`receiptMode/${user.salePointId}`, e.target.value)
  }

  const handleEdcChange = e => {
    setEdcMode(e.target.value)
    if (e.target.value === 'other') {
      window.configBridge.setConfig('edc', { path: edcValue })
    } else {
      window.configBridge.setConfig('edc', { path: e.target.value })
    }
  }

  const handleEdcValueChange = e => {
    setEdcValue(e.target.value)
    if (edcMode === 'other') {
      window.configBridge.setConfig('edc', { path: e.target.value })
    }
  }

  const handlePrinterChange = e => {
    setPrinterMode(e.target.value)
    if (e.target.value === 'other') {
      window.configBridge.setConfig('printer', { path: printerValue })
    } else {
      window.configBridge.setConfig('printer', { path: e.target.value })
    }
  }

  const handlePrinterValueChange = e => {
    setPrinterValue(e.target.value)
    if (edcMode === 'other') {
      window.configBridge.setConfig('printer', { path: e.target.value })
    }
  }

